"use client";

import React, { useState } from "react";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { motion } from "framer-motion";

const queryLanguages = [
  { value: "sql", label: "SQL" },
  { value: "postgresql", label: "PostgreSQL" },
  { value: "mysql", label: "MySQL" },
  { value: "mongodb", label: "MongoDB" },
  { value: "graphql", label: "GraphQL" },
  { value: "dynamodb", label: "DynamoDB" },
  { value: "cassandra", label: "Cassandra" },
  { value: "elasticsearch", label: "Elasticsearch" },
  { value: "redis", label: "Redis" },
  { value: "snowflake", label: "Snowflake" },
  { value: "bigquery", label: "BigQuery" },
  { value: "rest-api", label: "REST API curl" },
  { value: "json-api", label: "JSON:API" },
  { value: "databricks", label: "Databricks" },
];

interface QueryLanguageSelectorProps {
  value: string;
  onChange: (value: string) => void;
}

export function QueryLanguageSelector({ value, onChange }: QueryLanguageSelectorProps) {
  const [hovered, setHovered] = useState(false);

  return (
    <div className="w-full">
      <div className="mb-2 flex items-center justify-between">
        <Label htmlFor="query-language">Query language</Label>
      </div>
      <motion.div
        whileHover={{ scale: 1.01 }}
        whileTap={{ scale: 0.99 }}
        className="relative"
        onHoverStart={() => setHovered(true)}
        onHoverEnd={() => setHovered(false)}
      >
        <Select value={value} onValueChange={onChange}>
          <SelectTrigger 
            id="query-language" 
            className={`w-full transition-all ${
              hovered 
                ? "shadow-md bg-secondary/50" 
                : "shadow-sm"
            }`}
          >
            <SelectValue placeholder="Select a query language" />
          </SelectTrigger>
          <SelectContent className="max-h-[300px]">
            {queryLanguages.map((language) => (
              <SelectItem key={language.value} value={language.value}>
                {language.label}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
        {hovered && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="absolute inset-0 -z-10 bg-gradient-to-r from-rose-100/20 to-orange-100/20 blur-xl rounded-xl"
          />
        )}
      </motion.div>
    </div>
  );
} 