"use client";

import React, { useState } from "react";
import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { toast } from "sonner";
import { LanguageModelSelector } from "./language-model-selector";
import { QueryLanguageSelector } from "./query-language-selector";
import { SchemaInput } from "./schema-input";
import { QuestionInput } from "./question-input";
import { QueryResult } from "./query-result";

// Mock function for generating a query (in a real app, this would call an API)
const generateQueryMock = async (
  model: string,
  queryLanguage: string,
  schema: string,
  question: string
): Promise<string> => {
  // Simulate API call
  return new Promise((resolve) => {
    setTimeout(() => {
      switch (queryLanguage) {
        case "sql":
          resolve(`SELECT * FROM users 
WHERE created_at >= DATE_SUB(NOW(), INTERVAL 30 DAY);`);
          break;
        case "mongodb":
          resolve(`db.users.find({
  created_at: { 
    $gte: new Date(new Date().setDate(new Date().getDate() - 30)) 
  }
})`);
          break;
        case "graphql":
          resolve(`query RecentUsers {
  users(
    where: {
      created_at: { gte: "${new Date(new Date().setDate(new Date().getDate() - 30)).toISOString()}" }
    }
  ) {
    id
    name
    email
    created_at
  }
}`);
          break;
        case "dynamodb":
          resolve(`{
  "TableName": "Users",
  "FilterExpression": "created_at >= :thirty_days_ago",
  "ExpressionAttributeValues": {
    ":thirty_days_ago": {
      "S": "${new Date(new Date().setDate(new Date().getDate() - 30)).toISOString()}"
    }
  }
}`);
          break;
        default:
          resolve(`# Query for ${queryLanguage}
# Find all users who signed up in the last 30 days
# Using model: ${model}
# Schema: ${schema.substring(0, 30)}...`);
      }
    }, 2000);
  });
};

export function QueryGenerator() {
  const [languageModel, setLanguageModel] = useState("openai-gpt-3.5");
  const [queryLanguage, setQueryLanguage] = useState("sql");
  const [schema, setSchema] = useState("");
  const [question, setQuestion] = useState("");
  const [result, setResult] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleGenerate = async () => {
    if (!schema) {
      toast.error("Please provide a schema");
      return;
    }

    if (!question) {
      toast.error("Please provide a question");
      return;
    }

    setIsLoading(true);

    try {
      const generatedQuery = await generateQueryMock(
        languageModel,
        queryLanguage,
        schema,
        question
      );
      setResult(generatedQuery);
      toast.success("Query generated successfully!");
    } catch (error) {
      toast.error("Failed to generate query. Please try again.");
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="grid gap-8">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="grid gap-8 md:grid-cols-2"
      >
        <div>
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3, delay: 0.1 }}
          >
            <LanguageModelSelector
              value={languageModel}
              onChange={setLanguageModel}
            />
          </motion.div>
        </div>
        <div>
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3, delay: 0.2 }}
          >
            <QueryLanguageSelector
              value={queryLanguage}
              onChange={setQueryLanguage}
            />
          </motion.div>
        </div>
      </motion.div>
      
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3, delay: 0.3 }}
      >
        <SchemaInput value={schema} onChange={setSchema} />
      </motion.div>
      
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3, delay: 0.4 }}
      >
        <QuestionInput value={question} onChange={setQuestion} />
      </motion.div>
      
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3, delay: 0.5 }}
        className="flex justify-center"
      >
        <Button
          className="w-full md:w-1/2 lg:w-1/3 bg-gradient-to-r from-pink-500 to-pink-600 hover:from-pink-600 hover:to-pink-700 shadow-md hover:shadow-lg transition-all duration-300 h-12 text-lg font-medium"
          onClick={handleGenerate}
          disabled={isLoading}
        >
          Generate
        </Button>
      </motion.div>
      
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3, delay: 0.6 }}
      >
        <QueryResult
          queryLanguage={queryLanguage}
          result={result}
          isLoading={isLoading}
        />
      </motion.div>
    </div>
  );
} 