"use client";

import React, { useState } from "react";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { motion } from "framer-motion";

interface QuestionInputProps {
  value: string;
  onChange: (value: string) => void;
}

export function QuestionInput({ value, onChange }: QuestionInputProps) {
  const [focused, setFocused] = useState(false);

  return (
    <div className="w-full">
      <div className="mb-2 flex items-center justify-between">
        <Label htmlFor="question">Question</Label>
      </div>
      <p className="text-sm text-muted-foreground mb-3">
        What do you want to know about your data?
      </p>
      <motion.div
        className="relative"
        whileHover={{ scale: 1.005 }}
        animate={focused ? { scale: 1.005 } : { scale: 1 }}
      >
        <Textarea
          id="question"
          className={`min-h-[100px] transition-all ${
            focused ? "shadow-md border-primary/20" : "shadow-sm"
          }`}
          placeholder="e.g. Find all users who signed up in the last 30 days"
          value={value}
          onChange={(e) => onChange(e.target.value)}
          onFocus={() => setFocused(true)}
          onBlur={() => setFocused(false)}
        />
        {focused && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="absolute inset-0 -z-10 bg-gradient-to-r from-green-100/20 to-teal-100/20 blur-xl rounded-xl"
          />
        )}
      </motion.div>
    </div>
  );
} 