"use client";

import React, { useState } from "react";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { motion } from "framer-motion";

const models = [
  { value: "openai-gpt-3.5", label: "OpenAI GPT-3.5" },
  { value: "openai-gpt-4", label: "OpenAI GPT-4" },
  { value: "anthropic-claude-3", label: "Anthropic Claude 3" },
  { value: "google-gemini", label: "Google Gemini" },
];

interface LanguageModelSelectorProps {
  value: string;
  onChange: (value: string) => void;
}

export function LanguageModelSelector({ value, onChange }: LanguageModelSelectorProps) {
  const [hovered, setHovered] = useState(false);

  return (
    <div className="w-full">
      <div className="mb-2 flex items-center justify-between">
        <Label htmlFor="language-model">Language model</Label>
      </div>
      <motion.div
        whileHover={{ scale: 1.01 }}
        whileTap={{ scale: 0.99 }}
        className="relative"
        onHoverStart={() => setHovered(true)}
        onHoverEnd={() => setHovered(false)}
      >
        <Select value={value} onValueChange={onChange}>
          <SelectTrigger 
            id="language-model" 
            className={`w-full transition-all ${
              hovered 
                ? "shadow-md bg-secondary/50" 
                : "shadow-sm"
            }`}
          >
            <SelectValue placeholder="Select a language model" />
          </SelectTrigger>
          <SelectContent>
            {models.map((model) => (
              <SelectItem key={model.value} value={model.value}>
                {model.label}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
        {hovered && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="absolute inset-0 -z-10 bg-gradient-to-r from-blue-100/20 to-purple-100/20 blur-xl rounded-xl"
          />
        )}
      </motion.div>
    </div>
  );
} 